<template>
  <div class="tabs-panel" id="dietary-settings" v-if="selecttab" v-bind:class="{'is-active' : selecttab}">
    <div class="grid-x grid-margin-x">
      <div class="cell small-12 text-center">
        <p class="h3 cursive title line-left-right">Allergies</p>
      </div>
      <div class="cell small-12 xlarge-10 xlarge-offset-1">
        <div class="callout">
          <div class="grid-x grid-padding-x">
            <div class="cell small-12 large-10 large-offset-1">
              <p class="text-center">Please select all that apply</p>
              <fieldset>
                <ul class="large-list no-bullet grid-x small-up-2 smedium-up-3 medium-up-3 large-up-4 xlarge-up-5 xxlarge-up-6">
                  <!--BEGIN API LOOP - AccountPage-AllergyDefs - TopLevel: AllergryDefDesc1, AllergyDefID-->
                  <li class="cell" v-for="AllergyDef in AllergyDefs" v-bind:key="AllergyDef.allergydefid">
                    <div class="img-checkbox">
                      <input :id="'likes-'+AllergyDef.allergydefid" type="checkbox" />
                      <label :for="'likes-'+AllergyDef.allergydefid">
                        <div class="img">
                          <img :src="AllergyDef.imagefilepath" :alt="AllergyDef.alergydefdesc1">
                        </div>
                        <strong>{{AllergyDef.alergydefdesc1}}</strong>
                        <button class="button tiny muted" :data-open="AllergyDef.allergydefid+'-subcategories'" @click="opensubcategory(AllergyDef.allergydefid+'-subcategories')">Make Selections</button>
                      </label>
                    </div>
                  </li>
                  <!--END API LOOP-->
                </ul>
              </fieldset>
              <p class="text-center">
                <button class="button muted">Save Selections</button>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--START API LOOP: AccountPage-UserAllergies  -  OUTER LOOP FOR EVERY DISTINCT AllergyDefID-->

    <div v-for="obj in UserAllergies_distinct" :key="obj.allergydefid" class="reveal large" :id="obj.allergydefid+'-subcategories'" data-reveal data-animation-in="slide-in-down fast" data-animation-out="slide-out-up fast">
      <modal>
        <div slot="body" class="modal-body">
          <div class="grid-x grid-padding-x">
            <div class="cell small-12 large-10 large-offset-1" id="toggle-all" style="background-color:white">

              <p class="text-center title h3">{{obj.allergydefdesc1}}</p>
              <p class="text-center">Select the item(s) you are allergic to.</p>
              <hr />
              <fieldset>
                <ul class="small-checkboxes large-list no-bullet grid-x grid-margin-x small-up-1 smedium-up-2 medium-up-2">
                  <!--START API LOOP: AccountPage-UserAllergies  -  INNER LOOP-->
                  <li class="cell large-listitem" v-if="UserAllergy.allergydefid === obj.allergydefid" v-for="UserAllergy in UserAllergies" :key="UserAllergy.foodclassid">
                    <div class="custom-checkbox">
                      <input :id="'select-'+UserAllergy.foodclassid" type="checkbox" :checked="UserAllergy.ischecked==0?'':'checked'" />
                      <label :for="'select-'+UserAllergy.foodclassid"></label>
                    </div>
                    <span class="h4">{{UserAllergy.foodclassdesc1}}</span>
                  </li>
                  <!-- END INNER LOOP-->

                </ul>
              </fieldset>
              <p class="text-center">
                <button class="button muted" data-close aria-label="Close reveal">Save and Close</button>
              </p>
            </div>
          </div>
          <button class="close-button" data-close aria-label="Close reveal" type="button" @click="close(obj.allergydefid+'-subcategories')">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
      </modal>
    </div>

    <!--END OUTER LOOP-->

    <hr />
    <div class="grid-x grid-margin-x">
      <div class="cell small-12 text-center">
        <p class="h3 cursive title line-left-right">Food Likes</p>
      </div>
      <div class="cell small-12 xlarge-10 xlarge-offset-1">
        <div class="callout img-grid">
          <div class="grid-x grid-padding-x">
            <div class="cell small-12">
              <p class="text-center">Please select all that apply</p>
              <fieldset>
                <ul class="large-list no-bullet grid-x small-up-2 smedium-up-3 medium-up-3 large-up-4 xlarge-up-5 xxlarge-up-6">
                  <li class="cell" v-for="AllergyDef in AllergyDefs" v-bind:key="AllergyDef.allergydefid">
                    <div class="img-checkbox">
                      <input :id="'likes-'+AllergyDef.alergydefdesc1" type="checkbox" />
                      <label :for="'likes-'+AllergyDef.alergydefdesc1">
                        <div class="img"><img :src="AllergyDef.thumbnailimagefilepath" :alt="AllergyDef.alergydefdesc1+' icon'"></div>
                        <strong>{{AllergyDef.alergydefdesc1}}</strong>
                        <button class="button tiny muted" :data-open="AllergyDef.allergydefid+'-subcategories'" @click="opensubcategory(AllergyDef.allergydefid+'-subcategories')">Make Selections</button>
                      </label>
                    </div>
                  </li>

                </ul>
              </fieldset>
            </div>
          </div>
        </div>
      </div>
    </div>
    <hr />
    <div class="grid-x grid-margin-x">
      <div class="cell small-12 text-center">
        <p class="h3 cursive title line-left-right">Dietary Choices</p>
      </div>
      <div class="cell small-12 xlarge-10 xlarge-offset-1">
        <div class="callout img-grid">
          <div class="grid-x grid-padding-x">
            <div class="cell small-12">
              <p class="text-center">Please select all that apply</p>
              <fieldset>
                <ul class="large-list no-bullet grid-x small-up-2 smedium-up-3 medium-up-3 large-up-4 xlarge-up-5 xxlarge-up-6">
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-lactose-free" type="checkbox" />
                      <label for="diet-lactose-free">
                        <div class="img"><img src="assets/img/icons/lactose-free-darkgray.svg" alt="Lactose-Free icon"></div>
                        <strong>Lactose-Free</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-vegetarian" type="checkbox" />
                      <label for="diet-vegetarian">
                        <div class="img"><img src="assets/img/icons/vegetarian-darkgray.svg" alt="Vegetarian icon"></div>
                        <strong>Vegetarian</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-alcohol-free" type="checkbox" />
                      <label for="diet-alcohol-free">
                        <div class="img"><img src="assets/img/icons/alcohol-free-darkgray.svg" alt="Alcohol-Free icon"></div>
                        <strong>Alcohol-Free</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-vegan" type="checkbox" />
                      <label for="diet-vegan">
                        <div class="img"><img src="assets/img/icons/vegan-darkgray.svg" alt="Vegan icon"></div>
                        <strong>Vegan</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-low-carb" type="checkbox" />
                      <label for="diet-low-carb">
                        <div class="img"><img src="assets/img/icons/low-carb-darkgray.svg" alt="Low-Carb icon"></div>
                        <strong>Low-Carb</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-gluten-free" type="checkbox" />
                      <label for="diet-gluten-free">
                        <div class="img"><img src="assets/img/icons/gluten-free-darkgray.svg" alt="Gluten-Free icon"></div>
                        <strong>Gluten-Free</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-low-sodium" type="checkbox" />
                      <label for="diet-low-sodium">
                        <div class="img"><img src="assets/img/icons/low-sodium-darkgray.svg" alt="Low Sodium icon"></div>
                        <strong>Low Sodium</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-low-fat" type="checkbox" />
                      <label for="diet-low-fat">
                        <div class="img"><img src="assets/img/icons/low-fat-darkgray.svg" alt="Low Fat icon"></div>
                        <strong>Low Fat</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-pescatarian" type="checkbox" />
                      <label for="diet-pescatarian">
                        <div class="img"><img src="assets/img/icons/pescatarian-darkgray.svg" alt="Pescatarian icon"></div>
                        <strong>Pescatarian</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-high-fiber" type="checkbox" />
                      <label for="diet-high-fiber">
                        <div class="img"><img src="assets/img/icons/high-fiber-darkgray.svg" alt="High-Fiber icon"></div>
                        <strong>High-Fiber</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-fruitarian" type="checkbox" />
                      <label for="diet-fruitarian">
                        <div class="img"><img src="assets/img/icons/fruitarian-darkgray.svg" alt="Fruitarian icon"></div>
                        <strong>Fruitarian</strong>
                      </label>
                    </div>
                  </li>
                  <li class="cell">
                    <div class="img-checkbox">
                      <input id="diet-organic" type="checkbox" />
                      <label for="diet-organic">
                        <div class="img"><img src="assets/img/icons/organic-darkgray.svg" alt="Organic icon"></div>
                        <strong>Organic</strong>
                      </label>
                    </div>
                  </li>
                </ul>
              </fieldset>
              <p class="text-center">
                <button class="button muted">Save Selections</button>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Library components.
import axios from 'axios';
import { mapState } from 'vuex'
// Custom components.
import router from '../../router'
import Modal from '../Modal.vue'

export default {
  name: 'DietarySettings',
  props: ['selecttab'],
  components: {
    Modal
  },

  data() {
    return {
      ZNavKey: 'CE89FE19-8B65-4A2C-AEC5-59B40B8153C1',
      endpoint: 'https://api.fooduniverse.com/service/api/data/doquery',
      loading: false,
      AllergyDefs: {

      },
      UserAllergies: {
      },
      UserAllergies_distinct: [],
      token: '',
    }
  },
  created: function() {
    //this.search()
    this.fetch()
  },

  methods: {
    search(srhtext = '') {
      // alert('Home : ' + srhtext)
      //router.push({ name: 'vendorsearch', params: { srhtext: 'srhtext' }})

      router.push({ path: `/SearchResults` }) // -> /user/123
      //router.push({ path: '/vendorsearch/'+srhtext }) // -> /user/123

    },
    fetch() {

      if (this.loading)
        return;
      // this.ZNavKey = this.$route.params.ZNavKey
      //Get token.
      var config = {
        headers: {
          'Content-Type': "application/x-www-form-urlencoded"
        }
      };

      this.loading = true

      //get Access token by username and password.
      var data_array = {
        grant_type: 'password',
        username: 'saritasa@fooduniverse.com',
        password: 'Apple2018!'
      };

      const data_string = Object.keys(data_array).map((key) => {
        return encodeURIComponent(key) + '=' + encodeURIComponent(data_array[key]);
      }).join('&');

      axios({
        method: 'post',
        url: 'http://ab.fooduniverse.com/service/token',
        data: data_string,
        config
      }).then(response => {

        // Set token by username and password.
        this.token = response.data.access_token
        if (this.token == '')
          return
        console.log(this.token)
        config = {
          headers: {
            'Content-Type': "application/json",
            'Authorization': "bearer " + this.token
          }
        }

        axios.post(this.endpoint + '/AccountPage-AllergyDefs', {
          ZNavKey: this.ZNavKey,
          config
        }).then((response) => {
          this.AllergyDefs = response.data.Items;
        })

        axios.post(this.endpoint + '/AccountPage-UserAllergies', {
          ZNavKey: this.ZNavKey,
          config
        }).then((response) => {
          this.UserAllergies = response.data.Items;
          var curid = -1
          var i = 0
          var arr = []
          response.data.Items.forEach(function(item) {
            // console.log(this)
            if (item.allergydefid != curid) {
              arr.push(item)
              curid = item.allergydefid
            }
          });
          this.UserAllergies_distinct = arr
          // console.log(this.UserAllergies_distinct)
        })

      })
    },
    opensubcategory(id) {

      document.getElementById(id).style.visibility = "visible"
      document.getElementById(id).style.display = "block"
    },
    close(id) {
      document.getElementById(id).style.visibility = "hidden"
      document.getElementById(id).style.display = "none"
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
]


